@use "base/variables" as v;
@use "base/mixins" as m;

.community {
	padding: 30rem 0 20rem 0;
	text-align: center;

	&__boton {
		@include m.boton(v.$primario, v.$blanco);
		border-radius: 5rem;
		padding: 2rem 5rem;
		margin-top: 4rem;
		box-shadow: 0px 2px 5px 1px rgb(0 0 0 / 25%);
	}
}
